.header {
    display: grid;
    grid-template-columns: 80% 20%;
    grid-template-rows: auto auto;
    grid-row-gap: 20px;
    padding-top: 27px;

    &__top-box {
        grid-column: 1 / 3;
        grid-row: 1 / 2;
        display: flex;
        justify-content: space-between;
    }

    &__lang {
        font-weight: 700;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 1px;
        color: $gray-text;
        background-color: transparent;
        border-radius: 8px;
        padding: 5px 6px;

        &:hover {
            color: $white;
            background-color: $gray-lang-bg-50;
        }

        &--current{
            color: $white;
            background-color: $red;
        }
    }

    &__contact {
       display: flex;
    }

    &__contact-address,
    &__contact-fb {
        display: none;
    }

    &__contact-tel {
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        color: $gray-text;

        &:hover {
            color: $red;
        }
    }

    &__logo {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
    }

    &__menu {
        grid-column: 2 / 3;
        grid-row: 2 / 3;
        align-self: center;
        justify-self: flex-end;
    }

    &__menu-link {
        display: none;
    }

    &__menu-btn {
        width: 20px;
        height: 32px;
        background-image: url(../img/header-menu-hamburger.svg);
        background-repeat: no-repeat;
        background-position: center;
        opacity: 0.5;
    }

    &__menu-modal {
        display: none;
    }

    &__content-box {
        padding-top: 100px;
    }

    &__content-symbol {
        padding-top: 40px;
        background-image: url(../img/header-symbol-p.svg);
        background-repeat: no-repeat;
        background-position: top;
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 10px;
    }

    &__content-title {
        font-weight: 500;
        font-size: 38px;
        line-height: 45px;
        color: $white;
        text-align: center;
        padding-bottom: 200px;
    }

    &__content-btn {
        width: 42px;
        height: 42px;
        background-image: url(../img/main-arrow.svg);
        background-position: center;
        background-repeat: no-repeat;
        border: 1px solid $arrow-border;
        border-radius: 50%;
    }
}

@media (min-width: 500px) {
    .header {
        &__content-title {
            max-width: 445px;
        }
    }
}

@media (min-width: 768px) {
    .header {
        grid-template-columns: 1fr auto;
        grid-template-rows: auto auto 30px;
        grid-row-gap: 10px;
    
        &__top-box {
            grid-column: 1 / 3;
            grid-row: 1 / 2;
            justify-content: flex-start;
            align-items: center;
        }

        &__contact {
            margin-left: 10px;
            align-items: center;
        }
    
        &__contact-address,
        &__contact-fb {
            display: inline-flex;
        }

        &__contact-address {
            font-size: 16px;
            line-height: 19px;
            color: $gray-text;
            margin-right: 13px;
        }

        &__contact-tel {
            margin-right: 13px;
        }
    
        &__contact-address::after,
        &__contact-tel::after {
            content: "";
            display: inline-flex;
            width: 7px;
            height: 7px;
            background-image: url(../img/header-separator.svg);
            background-repeat: no-repeat;
            background-position: center;
            margin-left: 13px;
            margin-top: 7px;
        }

        &__logo {
            grid-column: 1 / 3;
            grid-row: 2 / 3;
        }

        &__fb-icon {
            width: 24px;
            height: 24px;
            fill: $gray-text;

            &:hover {
                fill: $white;
            }
        }

        &__menu {
            grid-column: 1 / 3;
            grid-row: 3 / 4;
            align-self: start;
            justify-self: flex-start;
        }
    
        &__menu-link {
            font-weight: 500;
            font-size: 18px;
            line-height: 21px;
            color: $white;
            margin-right: 15px;
            padding-bottom: 2px;
            display: inline-flex;

            &:hover {
                color: $white;
                border-bottom: 2px solid $white;
            }

            &:last-of-type {
                margin-right: 0;
            }
        }
    
        &__menu-btn {
            display: none;
        }
    }
}

@media (min-width: 992px) {
    .header {
        grid-template-columns: auto auto;
        grid-template-rows: auto 30px;
        grid-row-gap: 15px;
        padding-top: 50px;

        &__top-box {
            grid-column: 2 / 3;
            grid-row: 1 / 2;
            justify-content: flex-end;
        }

        &__lang,
        &__contact-address,
        &__contact-tel {
            color: $white;
        }

        &__fb-icon {
            fill: $white;

            &:hover {
                fill: $blue-link;
            }
        }
        
        &__logo {
            grid-column: 1 / 2;
            grid-row: span 2;
        }

        &__menu {
            grid-column: 2 / 3;
            grid-row: 2 / 3;
            justify-self: flex-end;
        }

        &__content-box {
            background-image: url(../img/header-symbol-bg.svg);
            background-repeat: no-repeat;
            background-position: top center;
            margin-top: 60px;
            padding-top: 122px;
        }

        &__content {
            padding-top: 20px;
        }

        &__content-title {
            padding-bottom: 290px;
        }
    }
}